  .c-mobile-menu {
    display: none;
  }
}

/* --- クリック前の円 透明 --- */
.c-mobile-menu::after {
  position: absolute;
  display: block;
  content: "";
  width: 85%;
  height: 85%;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: all 0.75s;
}

.c-mobile-menu__bar {
  display: block;
  width: 1.5rem;
  height: 0.125rem;
  background-color: #FFFFFF;
  border-radius: 5px;
}

.c-mobile-menu__bar:first-of-type {
  margin-bottom: 0.3125rem;
  transition: 0.3s ease-in;
}

.c-mobile-menu__bar:nth-of-type(2) {
  transition: 0.3s ease-in;
}

.c-mobile-menu__bar:last-of-type {
  margin-top: 0.3125rem;
  transition: 0.3s ease-in;
}

/* --- ハンバーガーメニュークリック後のアニメーション --- */
.c-mobile-menu.is-open .c-mobile-menu__bar:first-of-type {
  margin-bottom: -0.125rem;
  transform: rotate(-45deg);
  transition: 0.3s ease-in;
}
.c-mobile-menu.is-open .c-mobile-menu__bar:nth-of-type(2) {
  opacity: 0;
  transform: translateX(50%);
  transition: 0.3s ease-in;
}
.c-mobile-menu.is-open .c-mobile-menu__bar:last-of-type {
  margin-top: -0.125rem;
  transform: rotate(45deg);
  transition: 0.3s ease-in;
}

.c-mobile-menu.is-open::after {
  animation: rotateCircle 0.7s 0.25s forwards;
}

/* --- border-color 1:border-top 2: border-right 3: border-bottom 4: border-left 
0%から100％までの間に約2回転しながら、円の色を右→右＋下→右＋下＋左→右＋下＋左＋上と増やしながら、最後には真円になるアニメーション
--- */
@keyframes rotateCircle {
  0% {
    border-color: transparent;
    transform: rotate(0);
  }
  25% {
    border-color: transparent #fff transparent transparent;
  }
  50% {
    border-color: transparent #fff #fff transparent;
  }
  75% {
    border-color: transparent #fff #fff #fff;
  }
  100% {
    border-color: #fff;
    transform: rotate(-680deg);
  }
}
