/*******************************************
ローディング（画面読み込み用のscssファイル: Pace.jsのCSSファイルをカスタム）
*******************************************/
/* ---#global-container: body直下全てを囲んでいるボックス（ページ全体） --- */
/* --- ローディング完了後にbodyに付与されるクラス --- */
.pace-done #global-container {
  position: relative;
  transition: 0.9s ease-in;
}

.pace-done #global-container::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 500;
  /* ---both: 再生前、最初のキーフレームの状態を保持 && 再生後、最後のキーフレームの状態を保持 --- */
  animation: cover-slide 1.2s ease-out both -0.2s;
}
@media screen and (min-width: 768px) {
  .pace-done #global-container::after {
    animation-duration: 1.8s;
    animation-delay: -0.6s;
  }
}

@keyframes cover-slide {
  0% {
    left: 0;
    right: 0;
  }
  50% {
    left: 0;
    right: 0;
  }
  100% {
    left: 100%;
    right: 0;
  }
}
/* --- ローディング中にbodyに付与されるクラス --- */
.pace-running #global-container {
  opacity: 0;
}

.pace-running {
  background: linear-gradient(90deg, #4b6cb7, #182848);
}

.pace {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  position: fixed;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.pace .pace-progress {
  /* --- もともと付いているtranslate 3dを打ち消す --- */
  transform: none !important;
  text-align: center;
}

/* --- ローディングのパーセンテージ --- */
.pace .pace-progress:after {
  display: block;
  margin-top: 2.5rem;
  content: attr(data-progress-text);
  font-size: 1.5rem;
  font-weight: 400;
  color: #333333;
}
@media screen and (min-width: 768px) {
  .pace .pace-progress:after {
    margin-top: 6.25rem;
    font-size: 3.125rem;
  }
}

.pace-progress-inner {
  background-image: url(../images/common/logo-w-2.svg);
  background-repeat: no-repeat;
  background-position: 0 center;
  background-size: contain;
  /* --- 画面幅によってサイズも可変するように  高さもvwだと画面幅で可変する--- */
  max-width: 50rem;
  width: 46vw;
  height: 10vw;
  animation: loading-logo_scale 1s ease-out backwards;
}

.pace.pace-inactive {
  display: none;
}

@keyframes loading-logo_scale {
  0% {
    opacity: 0;
    transform: translateY(-20px) scale(1.3);
  }
  40% {
    opacity: 0.7;
  }
  60%, 100% {
    opacity: 1;
    transform: translateY(0) scale(1.6);
  }
}
